.App {
  text-align: center;
}

body {
  background-color: rgba(136, 81, 9, 0.5);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.productimage {
   height: auto;
  max-width: 50%;
}

.productobj {
  display: flex;
  border:5px #282c34 solid;
  flex-direction: column;
  align-items: center;
  width: 350px;
  height: 400px;
  background-color: rgba(0, 0, 0, 0.719);
  border-radius: 15px;
  margin: 9px;
  padding: 20px;
  font-size: 20px;
  color: #ffffff;


}



.productmap{
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap; 
  margin-left: 250px;
  margin-right:50px ;
  align-content: space-between;

}

.button{
  background-color: #282c34;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;

  font-size: 30px;

  cursor: pointer;
}

.button1{
  background-color: #282c34;
  border: none;
  color: white;
  font-size: 30px;
  margin-right: 1740px;

  cursor: pointer;
}

.buttoncart {
  background-color: #4f8aff;
  border: none;
  color: white;
  font-size: 16px;
  padding: 10px;
  font-weight: 2000;
  border-radius: 29px;
  margin: auto; 
}

.buttoncart:hover{
  transition: 1s;
  background-color:#61dafb;
  transform: scale(1.3);
    }

.Menushopcart {
  border:5px #282c34 solid;
  flex-direction: column;
  align-items: center;
  width: 370px;
  height: 600px;
  background-color: rgb(88, 88, 88);
  border-radius: 15px;
  margin: 5px;
  padding: 20px;
  font-size: 15px;
  color: #ffffff;
  position:absolute;
  top: 100px;
}

table {
  align-content: space-between;
  border-collapse:10px  collapse;
  width: 100%;
  border: 2px rgb(8, 0, 0) solid;
  top: 110px;
  right: 0px;
  position: absolute;
  }

th{

  border: 2px rgb(8, 0, 0) solid;
  background-color: rgb(136, 136, 136);
  }

.Finalprice {
  z-index: 10;
  top: 560px;
  left: 110px;
  position: absolute;
  color: white;
  
}

.Editproduct{
  position: absolute;
  top: 520px;
  left: 100px;
  font-size: 0px;
  border: #282c34 solid 6px;
}

.emptybutton {
  position: absolute;
  top: 115px;
  left: -40px;
  border-radius: 8px;
  font-size: 17px;
}

.emptybutton:hover {
background-color: #6092f7;
}

.checkout {
  position: absolute;
  top: 115px;
  left: 150px;
  border-radius: 8px;
  font-size: 17px;
  
}

.checkout:hover {
  background-color: #6092f7;
  }

.checkoutbox {
  border:5px #282c34 solid;
  width: 1100px;
  height: 850px;
  background-color: rgb(88, 88, 88);
  border-radius: 100px;
  margin: 50px;
  padding: 20px;
  font-size: 15px;
  color: #ffffff;
  position:absolute;
  top: 200px;
  left: 400px;
  
}

.checkoutboxes {
  text-align: center;

  width: 400px;
  border: 2px rgb(71, 71, 71) solid;
  background-color: rgb(228, 228, 228);
}

.Submitbutton1 {
  font-size: 15px;
}

.Ordermsg {

  position:absolute;
  top: 150px;
  left: 400px;  
  width: 1200px;
  height: 960px;  
  font-size: 18px;
  border:5px #282c34 solid;
  background-color: rgb(88, 88, 88);
  color: #ffffff
}

h3 {
  color: #00c3ff;
  font-size: 18px;
}

.submitcheckbut{
  border-radius: 9px;
  font-size: 14px;
}

.submitcheckbut:hover{
  background-color: #00c3ff;
  transform: scale(1.1);
}

.summarybutton{
  border-radius: 9px;
  font-size: 14px;
}

.summarybutton:hover{
  background-color: #00c3ff;
  transform: scale(1.1);
}